.arrow {
    -webkit-align-self: end;
            align-self: end;
    width: 50%;
    height: 15%;
    margin-bottom: 4em;
    position: absolute;
    bottom: 0px;
    left: -3%;
    border-right: 2px solid #d0ff00;
    -webkit-animation: arrow-animation ease 1.5s;
            animation: arrow-animation ease 1.5s;
}
.arrow::after {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 11px 11px 0px 11px;
    border-color: #d0ff00 transparent transparent transparent;
    right: -0.7em;
    bottom: -2px;
}
@-webkit-keyframes arrow-animation {
    0% {
        bottom: 70px;
        opacity: 0.2;
    }

    100% {
        bottom: 5px;
        opacity: 1;
    }
}
@keyframes arrow-animation {
    0% {
        bottom: 70px;
        opacity: 0.2;
    }

    100% {
        bottom: 5px;
        opacity: 1;
